          active-name="1"
          @on-select="name=>{name=='logout' && logout()}"
        >
          <div class="layout-nav">
            <MenuItem
              v-for="ele in types"
              :key="ele.id"
              :to="`/blogs/${ele.id}`"
              :name="ele.id"
              style="display:flex;align-items: center;"
            >
              <i v-html="ele.svg" class="ivu-icon"></i>
              <span>{{ele.name}}</span>
            </MenuItem>
