<script>
import axios from "axios";
export default {
  data() {
    return {
      user: {},
      types: []
    };
  },
  created() {
    this.getUser();
    this.getTypes();
  },
  methods: {
    getUser() {
      axios("/api/getCurrentUser").then(res => {
        // this.user
        this.user = res.data;
        console.log(this.user);
      });
    },
    async getTypes() {
      const res = await axios.get("/api/types");
      this.types = res.data;
    },
    async logout() {
      await axios("/api/logout");
      location.reload();
    }
  }
};
</script>

